* {
  box-sizing: border-box;
}
.form {
  font-family: "Arial", "Helvetica", sans-serif;
  font-size: 20px;
  max-width: 600px;
  background-color: #efffff;
  border: 1px solid #bbbbbb;
  border-radius: 10px;
  margin-top: 50px;
  padding: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.form__submit-button {
  border: 1px solid #bbbbbb;
  background-color: #efefef;
  border-radius: 5px;
  padding: 10px;
}
.form__submit-button:hover {
  background-color: #ffffff;
}
.container {
  max-width: 600px;
  margin: 0px auto;
}
.range {
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin-bottom: 40px;
}
.range__input {
  padding: 10px;
  width: 100%;
  border: 1px solid #bbbbbb;
  border-radius: 5px;
}
.range__bar {
  height: 5px;
  margin: 10px 30px;
  display: flex;
  background-color: #dddddd;
  grid-column-start: 1;
  grid-column-end: -1;
}
.range__scale {
  height: 200%;
  flex-basis: 100%;
  transform: translateY(-25%);
  border-radius: 10px;
  background-color: #aaaaff;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.range__toggle {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  margin: -2.5px;
  background-color: #efefef;
  border: 1px solid #bbbbbb;
  cursor: pointer;
}
.range__toggle:hover {
  background-color: #ffffff;
}
